import React, {useState} from 'react';
import {FormControl, InputGroup, Nav, Navbar} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import {BsSearch} from 'react-icons/bs'
import {Link} from "react-router-dom";
import {getCookie, removeCookie} from "typescript-cookie";
import {createConfiguration, DefaultApi, HttpMethod, TokenSchema} from "../../backend-api";
import {lightGreen} from '@mui/material/colors';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import LoginIcon from '@mui/icons-material/Login';
import LogoutIcon from '@mui/icons-material/Logout';
import QueueMusicIcon from '@mui/icons-material/QueueMusic';

import Select, {SelectChangeEvent} from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import {default as MuiFormControl} from '@mui/material/FormControl';
import {Box, InputLabel} from "@mui/material";
import FilterListIcon from '@mui/icons-material/FilterList';
import ColorButton from "./ColorButton";
import SearchButton from './SearchButton';

function Header() {
    const [searchQuery, setSearchQuery] = useState<string>("");
    const [selectedFilter, setSelectedFilter] = useState('artist');
    const stateData = {query: searchQuery, filter: selectedFilter};
    const filters = [
        {label: 'Artist', value: 'artist'},
        {label: 'Title', value: 'title'},
        {label: 'Genre', value: 'genre'}
    ];

    function logout() {
        const configuration = createConfiguration();
        configuration.baseServer.makeRequestContext("http://localhost:8080/", HttpMethod.POST);
        const apiInstance = new DefaultApi(configuration);
        let token: string = getCookie("token") as string;

        let logoutData: TokenSchema = {
            token: token
        }

        apiInstance.logout(logoutData).then((data: any) => {
            removeCookie("token");
            window.location.reload();
        }).catch((error: any) => {
            console.error(error);
        });
    }

    function logOutButton() {
        let token: string = getCookie("token") as string;

        if (token === undefined) {
            return (
                <Link to={'/login'} className="link">
                    <ColorButton variant="outlined" endIcon={<LoginIcon/>}>Login</ColorButton>
                </Link>)
        } else {
            return (
                <>
                    <Link to={'/playlist'} className="link">
                        <ColorButton variant="outlined" startIcon={<QueueMusicIcon/>}>Playlist</ColorButton>
                    </Link>
                    <Link to={'/'} className="link">
                        <ColorButton onClick={logout} variant="outlined" endIcon={<LogoutIcon/>}>Logout</ColorButton>
                    </Link>
                </>
            )
        }
    }

    const handleChange = (event: SelectChangeEvent) => {
        setSelectedFilter(event.target.value);
    };

    return (
        <div>
            <Navbar sticky="top" className="color-nav py-0">
                <Nav className="col-3 logoNavStyle">
                    <Link to={'/'} className="link">
                        <img
                            src="logowhite.png"
                            width="200"
                            height="40"
                            className="d-inline-block align-top img-fluid logoStyle"
                            alt="Music4You"
                        />
                    </Link>
                </Nav>

                <Nav className="col-5 justify-content-center">
                    <Box sx={{minWidth: "90px"}}>
                        <MuiFormControl color="success" variant="outlined" size="small">
                            <InputLabel sx={{color: lightGreen[500]}}>Filter <FilterListIcon/></InputLabel>
                            <Select
                                onChange={handleChange}
                                sx={{minWidth: "85px", color: lightGreen[500], textAlign: "center"}}
                            >
                                {filters.map((filter) => (
                                    <MenuItem value={filter.value}>{filter.label}</MenuItem>
                                ))}
                            </Select>
                        </MuiFormControl>
                    </Box>
                    <InputGroup>
                        <FormControl
                            placeholder="Search..."
                            aria-label="Recipient's username"
                            aria-describedby="basic-addon2"
                            onChange={e => setSearchQuery(e.target.value)}
                        />
                    </InputGroup>
                    <Link to={'/results'} state={stateData} className="link">
                        <SearchButton variant="outlined" id="search-button">
                            <BsSearch size={20}></BsSearch>
                        </SearchButton>
                    </Link>
                </Nav>

                <Nav className="col-4 justify-content-evenly">
                    <Link to={'/shopping-cart'} className="link">
                        <ColorButton variant="outlined" startIcon={<ShoppingCartIcon/>}>
                            Shopping Cart
                        </ColorButton>
                    </Link>
                    {logOutButton()}
                </Nav>
            </Navbar>
        </div>
    );
}

export default Header;