import {Nav, Navbar} from "react-bootstrap";
import React from "react";
import {Link} from "react-router-dom";

function PaymentProcessHeader() {
    return (
        <div>
            <Navbar sticky="top" className="color-nav py-0 justify-content-center">
                <Nav className="col-4 logoNavStyle">
                    <Link to={'/'}>
                        <img
                            src="logowhite.png"
                            width="200"
                            height="40"
                            className="d-inline-block align-top img-fluid logoStyle"
                            alt="Music4You"
                        />
                    </Link>
                </Nav>
            </Navbar>
        </div>
    );
}

export default PaymentProcessHeader;