import React, {useState} from "react";
import {Card, Form, FormControl, Row} from "react-bootstrap";
import {Link, useNavigate} from "react-router-dom";
import {createConfiguration, DefaultApi, HttpMethod, LoginSchema} from "../../backend-api";
import {setCookie} from 'typescript-cookie'
import {Box} from "@mui/material";
import LoginPageButton from "./LoginPageButton";

function LoginpageContent() {

    const [loginError, setLoginError] = useState<string>("");
    const [userId, setUserId] = useState<string>("");
    const [password, setPassword] = useState<string>("");


    const configuration = createConfiguration();
    configuration.baseServer.makeRequestContext("http://localhost:8080/", HttpMethod.POST);
    const apiInstance = new DefaultApi(configuration);

    const navigate = useNavigate();

    const handleSubmit = (): void => {

        let loginData: LoginSchema = {
            userId: userId,
            password: password
        }

        apiInstance.login(loginData).then((data: any) => {
            setCookie("token", data.token, {expires: 1, path: "/"});
            navigate('/');
        }).catch((error: any) => {
            console.error(error);
            setLoginError('Invalid username or password. Please try again.');
        });

    }

    return (
        <div style={{justifyContent: 'center'}} className="content">
            <Card border="light" style={{paddingLeft: '40rem', paddingRight: '40rem'}}>
                <Card.Body>
                    <Form>
                        <Form.Group as={Row} className="mb-3">
                            <FormControl type="text" placeholder="User Id" onChange={e => setUserId(e.target.value)}/>
                        </Form.Group>
                        <Form.Group as={Row} className="mb-3">
                            <FormControl type="password" placeholder="Password"
                                         onChange={e => setPassword(e.target.value)}/>
                        </Form.Group>
                    </Form>
                    <Row>
                        <span className="text-danger text-center">{loginError}</span>
                    </Row>
                    <Box
                        component="span"
                        m={1}
                        display="flex"
                        justifyContent="space-between"
                        alignItems="center"
                    >
                        <Link to="/" className="link">
                            <LoginPageButton variant="contained">Cancel</LoginPageButton>
                        </Link>
                        <LoginPageButton variant="contained" onClick={() => handleSubmit()}>Login</LoginPageButton>
                    </Box>
                </Card.Body>
            </Card>
        </div>
    );
}

export default LoginpageContent;