import React, {useEffect} from "react";
import {Song} from "../Song";
import {ShoppingCartInstance} from "./ShoppingCartInstance";
import ShoppingCartButton from "./ShoppingCartButton";
import RemoveShoppingCartIcon from '@mui/icons-material/RemoveShoppingCart';
import ShoppingCartContent from "./ShoppingCartContent";

function ShoppingCartEntry({song, pos}: { song: Song, pos: number }) {

    function deleteItem(item: Song) {
        const shoppingCartInstance = ShoppingCartInstance.getInstance();
        shoppingCartInstance.removeItem(item);
    }

    return (
        <tr>
            <td>{pos}</td>
            <td>{song.title}</td>
            <td>{song.artist}</td>
            <td>{song.price}</td>
            <td>
                <ShoppingCartButton onClick={() => deleteItem(song)}><RemoveShoppingCartIcon /></ShoppingCartButton>
            </td>
        </tr>
    )
}

export default ShoppingCartEntry;