import { ResponseContext, RequestContext, HttpFile } from '../http/http';
import * as models from '../models/all';
import { Configuration} from '../configuration'

import { DigitalSongDTO } from '../models/DigitalSongDTO';
import { DigitalSongListSchema } from '../models/DigitalSongListSchema';
import { LoginSchema } from '../models/LoginSchema';
import { ShoppingCartDigitalSongSchema } from '../models/ShoppingCartDigitalSongSchema';
import { ShoppingCartSoundCarrierSchema } from '../models/ShoppingCartSoundCarrierSchema';
import { SoundCarrierDTO } from '../models/SoundCarrierDTO';
import { TokenSchema } from '../models/TokenSchema';
import { ObservableDefaultApi } from './ObservableAPI';

import { DefaultApiRequestFactory, DefaultApiResponseProcessor} from "../apis/DefaultApi";
export class PromiseDefaultApi {
    private api: ObservableDefaultApi

    public constructor(
        configuration: Configuration,
        requestFactory?: DefaultApiRequestFactory,
        responseProcessor?: DefaultApiResponseProcessor
    ) {
        this.api = new ObservableDefaultApi(configuration, requestFactory, responseProcessor);
    }

    /**
     * Login with username and password, returns token as cookie
     * @param loginSchema 
     */
    public login(loginSchema?: LoginSchema, _options?: Configuration): Promise<TokenSchema> {
        const result = this.api.login(loginSchema, _options);
        return result.toPromise();
    }

    /**
     * Logout by invalidating the token
     * @param tokenSchema 
     */
    public logout(tokenSchema?: TokenSchema, _options?: Configuration): Promise<void> {
        const result = this.api.logout(tokenSchema, _options);
        return result.toPromise();
    }

    /**
     * Make a purchase with the given digital songs
     * @param shoppingCartDigitalSongSchema 
     */
    public purchaseDigitalSong(shoppingCartDigitalSongSchema?: ShoppingCartDigitalSongSchema, _options?: Configuration): Promise<void> {
        const result = this.api.purchaseDigitalSong(shoppingCartDigitalSongSchema, _options);
        return result.toPromise();
    }

    /**
     * Make a purchase with the given Sound Carriers
     * @param shoppingCartSoundCarrierSchema 
     */
    public purchaseSoundCarrier(shoppingCartSoundCarrierSchema?: ShoppingCartSoundCarrierSchema, _options?: Configuration): Promise<void> {
        const result = this.api.purchaseSoundCarrier(shoppingCartSoundCarrierSchema, _options);
        return result.toPromise();
    }

    /**
     * Get Sound Carriers by album name
     * @param album 
     * @param pageNr 
     */
    public searchByAlbum(album: string, pageNr?: string, _options?: Configuration): Promise<SoundCarrierDTO> {
        const result = this.api.searchByAlbum(album, pageNr, _options);
        return result.toPromise();
    }

    /**
     * Get songs by artist name
     * @param artist 
     * @param pageNyr 
     */
    public searchByArtist(artist: string, pageNyr?: string, _options?: Configuration): Promise<DigitalSongListSchema> {
        const result = this.api.searchByArtist(artist, pageNyr, _options);
        return result.toPromise();
    }

    /**
     * Get Sound Carriers by artist name
     * @param artist 
     * @param pageNr 
     */
    public searchByArtist1(artist: string, pageNr?: string, _options?: Configuration): Promise<SoundCarrierDTO> {
        const result = this.api.searchByArtist1(artist, pageNr, _options);
        return result.toPromise();
    }

    /**
     * Get songs by genre
     * @param genre 
     * @param pageNr 
     */
    public searchByGenre(genre: string, pageNr?: string, _options?: Configuration): Promise<DigitalSongListSchema> {
        const result = this.api.searchByGenre(genre, pageNr, _options);
        return result.toPromise();
    }

    /**
     * Get songs by song title
     * @param song 
     * @param pageNr 
     */
    public searchBySong(song: string, pageNr?: string, _options?: Configuration): Promise<DigitalSongListSchema> {
        const result = this.api.searchBySong(song, pageNr, _options);
        return result.toPromise();
    }

    /**
     * Get Sound Carriers by song name
     * @param song 
     * @param pageNr 
     */
    public searchBySong1(song: string, pageNr?: string, _options?: Configuration): Promise<SoundCarrierDTO> {
        const result = this.api.searchBySong1(song, pageNr, _options);
        return result.toPromise();
    }


}



