import React from "react";
import {Table} from "react-bootstrap";
import {Link} from "react-router-dom";
import ShoppingCartEntry from "./ShoppingCartEntry";
import {ShoppingCartInstance} from "./ShoppingCartInstance";
import {Song} from "../Song";
import {getCookie} from "typescript-cookie";

import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import ShoppingCartButton from "./ShoppingCartButton";

function ShoppingCartContent() {
    const shoppingCartInstance = ShoppingCartInstance.getInstance();
    const songs: Song[] = shoppingCartInstance.song;

    function checkout() {
        let token: string = getCookie("token") as string;
        if (token === undefined) {
            return (
                <Link to={'/login'} className="link">
                    <ShoppingCartButton variant="outlined" endIcon={<ShoppingCartCheckoutIcon/>}>Checkout</ShoppingCartButton>
                </Link>)
        } else {
            return (
                <Link to={'/checkout'} className="link">
                    <ShoppingCartButton variant="outlined" endIcon={<ShoppingCartCheckoutIcon/>}>Checkout</ShoppingCartButton>
                </Link>)
        }
    }

    return (
        <div className="content">
            <div>
                <h1 style={{color: "black", textAlign: "center"}}>Shopping Cart</h1>
            </div>
            <div>
                <Table striped bordered hover>
                    <thead>
                    <tr>
                        <th>#</th>
                        <th>Song</th>
                        <th>Artist</th>
                        <th>Price</th>
                        <th>#</th>
                    </tr>
                    </thead>
                    <tbody>
                    {songs.map((data, i) => (
                        <ShoppingCartEntry song={songs[i]} pos={i + 1}/>
                    ))}
                    </tbody>
                </Table>
                <div style={{textAlign: "center"}}>{checkout()}</div>
            </div>
        </div>
    )
}

export default ShoppingCartContent;