import React from "react";
import PaymentProcessHeader from "../component/payment/PaymentProcessHeader";
import PaymentProcessComponent from "../component/payment/PaymentProcessComponent";
import Footer from "../component/homepage/Footer";


function PaymentProcess() {
    return (
        <React.Fragment>
            <PaymentProcessHeader/>
            <PaymentProcessComponent/>
            <Footer/>
        </React.Fragment>
    );
}

export default PaymentProcess;