import React from "react";
import {BsInstagram} from 'react-icons/bs'
import {FiFacebook, FiTwitter} from "react-icons/fi";


function Footer() {
    return (
        <footer className="color-footer page-footer">
            <div className="container-fluid text-end text-md-end">
                <div className="row">
                    <div className="col-md-6 mt-md-0 mt-3"></div>

                    <div className="col-md-3 mb-md-0 mb-3"></div>

                    <div className="col-md-3 mb-md-0 mb-3 ">
                        <br/>
                        <ul className="list-unstyled">
                            <BsInstagram size={20} className="col-md-1"/>
                            <FiTwitter size={20} className="col-md-1"/>
                            <FiFacebook size={20} className="col-md-1"/>
                        </ul>
                    </div>
                    <div className="col-md-12 mb-md-0 mb-3">
                        <p>  2022 Copyright: Music4You GmbH</p>

                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer;