import React, {useEffect, useState} from "react";
import '../../App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Card} from 'react-bootstrap';
import SearchResultEntry from "./SearchResultEntry";
import Pagination from "./Pagination";
import {useLocation} from "react-router-dom";
import {Song, SongList} from "../Song";


function SearchResultContent() {
    //Pagination
    const firstIndex = 0;
    const [pageSize, setPageSize] = React.useState(5);
    const [page, setPage] = React.useState(1);
    const [totalResults, setTotalResults] = useState(0);

    //Search results from backend
    const [searchResult, setSearchResult] = useState<SongList>({digitalSongDTOs: []});

    const location = useLocation();
    const stateData: any = location.state;     //search query from Header

    useEffect(() => {
        switch (stateData.filter) {
            case "artist":
                //fetch songs
                fetch("http://localhost:8080/api/v1/song/search/artist/" + stateData.query + "?pageNr=" + String(page) + "&pageSize=" + String(pageSize))
                    .then((response) => response.json())
                    .then((data: SongList) => setSearchResult(data))
                    .catch((err) => console.log('Error: ' + err));


                //fetch totalResults
                fetch("http://localhost:8080/api/v1/song/search/artist/total-results/" + stateData.query)
                    .then((response) => response.json())
                    .then((result: { results: number }) => setTotalResults(result.results))
                    .catch((err) => console.log('Error: ' + err));
                break;
            case "title":
                //fetch songs
                fetch("http://localhost:8080/api/v1/song/search/song/" + stateData.query + "?pageNr=" + String(page) + "&pageSize=" + String(pageSize))
                    .then((response) => response.json())
                    .then((data: SongList) => setSearchResult(data))
                    .catch((err) => console.log('Error: ' + err));

                //fetch totalResults
                fetch("http://localhost:8080/api/v1/song/search/song/total-results/" + stateData.query)
                    .then((response) => response.json())
                    .then((result: { results: number }) => setTotalResults(result.results))
                    .catch((err) => console.log('Error: ' + err));
                break;
            case "genre":
                //fetch songs
                fetch("http://localhost:8080/api/v1/song/search/genre/" + stateData.query + "?pageNr=" + String(page) + "&pageSize=" + String(pageSize))
                    .then((response) => response.json())
                    .then((data: SongList) => setSearchResult(data))
                    .catch((err) => console.log('Error: ' + err));

                //fetch totalResults
                fetch("http://localhost:8080/api/v1/song/search/genre/total-results/" + stateData.query)
                    .then((response) => response.json())
                    .then((result: { results: number }) => setTotalResults(result.results))
                    .catch((err) => console.log('Error: ' + err));
                break;
            default:
                console.log("No such filter exists!");
                break;
        }
    }, [stateData.query, page, pageSize]);


    //Pagination
    const [data, setData] = React.useState(searchResult.digitalSongDTOs.slice(firstIndex, pageSize));
    React.useEffect(() => {
        setData(searchResult.digitalSongDTOs.slice(0, pageSize));
    }, [pageSize, searchResult]);

    const handleChange = (e: any) => {
        let value = parseInt(e.target.value, 10);
        setPage(value);
        setData(searchResult.digitalSongDTOs.slice(firstIndex + pageSize * (value - 1), pageSize * value));
    };

    //Change width
    const changeWidth = (e: any) => {
        setPageSize(parseInt(e.target.value, 10));
    };

    console.log(totalResults);

    return (
        <div className="content">
            <div>
                <h1 style={{color: "black", textAlign: "center"}}>Search results</h1>
            </div>
            <div>
                <Card>
                    <Card.Body>
                        <div className="search-result-content search-summary">
                            <h3>Results for <strong>{stateData.query}</strong></h3>
                            <p>Showing {1}-{pageSize} out of {totalResults} results</p>
                        </div>
                        <ul className="lister">
                            {data.map((data: Song, i) => (
                                <li key={i} className="lists">
                                    <SearchResultEntry song={data}/>
                                </li>
                            ))}
                        </ul>
                        <div className="pagenum">
                            <label>Page: {page}</label>
                        </div>
                        <Pagination
                            count={Math.ceil(totalResults / pageSize)}       //TODO:tot results hier
                            handleChange={handleChange}
                        />
                        <div className="form-group mt-2">
                            <label className="form-label">Items per Page</label>
                            <input
                                style={{width: 60, textAlign: "center"}}
                                className="form-control"
                                type="number"
                                onChange={changeWidth}
                                value={pageSize}
                                min={1}
                            />
                        </div>
                    </Card.Body>
                </Card>
            </div>
        </div>
    )
}

export default SearchResultContent;