import {Song} from "../Song";

export class ShoppingCartInstance {
    private static instance: ShoppingCartInstance;
    private _song: Song[];


    private constructor() {
        if (localStorage.getItem("songs") === null) {
            this._song = [];
        } else {
            this._song = JSON.parse(localStorage.getItem("songs") || '{}');
        }
    }

    public static getInstance(): ShoppingCartInstance {
        if (!ShoppingCartInstance.instance) {
            ShoppingCartInstance.instance = new ShoppingCartInstance();
        }
        return ShoppingCartInstance.instance;
    }

    public addItem(song: Song) {
        if (this._song.indexOf(song) === -1) {
            this._song.push(song);
            localStorage.setItem("songs", JSON.stringify(this._song));
        }
    }

    public removeItem(song: Song) {
        this._song.forEach((value, index) => {
            if (value == song) this._song.splice(index, 1);
        });
        localStorage.setItem("songs", JSON.stringify(this._song));
    }

    public clear() {
        this._song = [];
        localStorage.setItem("songs", JSON.stringify(this._song));
    }

    get song(): Song[] {
        return this._song;
    }


}