import React, {useState} from "react";
import {Card, Form, FormControl, Modal, Row} from "react-bootstrap";
import {Link} from "react-router-dom";
import {PurchaseSchema, Song} from "../Song";
import {ShoppingCartInstance} from "../shoppingcart/ShoppingCartInstance";
import {getCookie} from "typescript-cookie";
import {Box} from "@mui/material";
import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import CancelIcon from '@mui/icons-material/Cancel';
import HomeIcon from '@mui/icons-material/Home';
import PaymentCancelButton from "./PaymentCancelButton";
import PaymentConfirmButton from "./PaymentConfirmButton";

function PaymentProcessComponent() {

    const [show, setShow] = useState(false);
    const [creditCardNumber, setCreditCardNumber] = useState<string>('');
    const [cvc, setCvc] = useState<string>('');
    const [purchaseMessage, setPurchaseMessage] = useState('');

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    function purchase() {
        const shoppingCartInstance = ShoppingCartInstance.getInstance();
        const songs: Song[] = shoppingCartInstance.song;
        const songIds: string[] = songs.map((song, i) => song.id)


        const purchaseSchema: PurchaseSchema = {
            cartSongIds: songIds,
            creditCardNumber: creditCardNumber,
            cvc: cvc,
            token: getCookie("token") as string
        };

        const requestOptions = {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(purchaseSchema)
        };

        fetch("http://localhost:8080/api/v1/song/purchase", requestOptions)
            .then(function (res) {
                if (res.ok) {
                    shoppingCartInstance.clear();
                    setPurchaseMessage('Purchase successful');
                } else {
                    setPurchaseMessage('Purchase failed');
                }
                handleShow();
            })
            .catch((err) => console.log('Error: ' + err));
    }

    return (
        <div style={{justifyContent: 'center'}} className="content">
            <Card border="light" style={{paddingLeft: '37rem', paddingRight: '37rem'}}>
                <Card.Body>
                    <Form>
                        <Form.Group as={Row} className="mb-3">
                            <FormControl type="text" placeholder="Credit Card Number..."
                                         onChange={e => setCreditCardNumber(e.target.value)}/>
                        </Form.Group>
                        <Form.Group as={Row} className="mb-3">
                            <FormControl type="text" placeholder="CVC..." onChange={e => setCvc(e.target.value)}/>
                        </Form.Group>
                    </Form>
                    <Row>
                        <span className="text-danger text-center"></span>
                    </Row>
                    <Box
                        component="span"
                        m={1}
                        display="flex"
                        justifyContent="space-between"
                        alignItems="center"
                    >
                        <Link to="/" className="link">
                            <PaymentCancelButton variant="outlined" startIcon={<CancelIcon/>}>Cancel</PaymentCancelButton>
                        </Link>
                        <PaymentConfirmButton variant="outlined" endIcon={<ShoppingCartCheckoutIcon/>}
                                     onClick={() => purchase()}>Purchase</PaymentConfirmButton>
                        <Modal show={show} onHide={handleClose}>
                            <Modal.Header closeButton>
                                <Modal.Title>{purchaseMessage}</Modal.Title>
                            </Modal.Header>
                            <Modal.Footer>
                                <Link to={'/'} className="link">
                                    <PaymentConfirmButton variant="outlined" onClick={handleClose} endIcon={<HomeIcon/>}>
                                        Back to Homepage
                                    </PaymentConfirmButton>
                                </Link>
                            </Modal.Footer>
                        </Modal>
                    </Box>
                </Card.Body>
            </Card>
        </div>
    );
}

export default PaymentProcessComponent;