import React, {useEffect, useState} from "react";
import PlaylistTable from "./PlaylistTable";
import {getCookie} from "typescript-cookie";
import ReactJkMusicPlayer from "react-jinke-music-player";
import "react-jinke-music-player/assets/index.css";
import {PlaylistEntry, PlaylistSong} from "../Song";
import DownloadIcon from '@mui/icons-material/Download';
import PlaylistDownloadButton from "./PlaylistDownloadButton";


function PlaylistContent() {
    const [cells, setCells] = useState<PlaylistEntry[]>([]);
    const [playlist, setPlaylist] = useState<PlaylistSong[]>([]);
    const token: string = getCookie("token") as string;

    const getData = async () => {

        const requestOptions = {
            method: 'POST',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({token: token})
        };

        const resp = await fetch("http://localhost:8081/api/v1/user/playlist/", requestOptions);
        const pldata: any = await resp.json();

        function DownloadSong(id: string, title: string) {
            fetch('http://localhost:8082/songs/download?songId=' + id, requestOptions)
                .then((response) => {
                    if (response.ok) {
                        response.blob().then((blob) => {
                            // Create blob link to download
                            const url = window.URL.createObjectURL(
                                new Blob([blob]),
                            );
                            const link = document.createElement('a');
                            link.href = url;
                            link.setAttribute(
                                'download',
                                title + '.mp3',
                            );

                            // Append to html link element page
                            document.body.appendChild(link);

                            // Start download
                            link.click();

                            // Clean up and remove the link
                            // @ts-ignore
                            link.parentNode.removeChild(link);
                        })
                    }
                });
        }

        pldata.forEach((obj: any) => obj.download =
            <PlaylistDownloadButton onClick={() => DownloadSong(obj.songId.toString(), obj.title.toString())}
                         startIcon={<DownloadIcon/>}>Download</PlaylistDownloadButton>);

        setCells(pldata);
    };

    const fillPlaylist = () => {
        let playlistSongs: PlaylistSong[] = [];
        let playlistEntries: PlaylistEntry[] = cells;

        playlistEntries.forEach((song: PlaylistEntry) => {
                let newSong: PlaylistSong = {
                    name: song.title,
                    singer: song.artist,
                    cover: "https://music-shop-songs.s3.eu-central-1.amazonaws.com/covers/" + song.songId + ".jpg",
                    musicSrc: "https://music-shop-songs.s3.eu-central-1.amazonaws.com/songs/" + song.songId + ".mp3"
                };
                playlistSongs.push(newSong);
            }
        );
        setPlaylist(playlistSongs);
    }

    const columns = React.useMemo(
        () => [
            {
                Header: "SongId",
                accessor: "songId" // accessor is the "key" in the data
            },
            {
                Header: "Title",
                accessor: "title" // accessor is the "key" in the data
            },
            {
                Header: "Artist",
                accessor: "artist"
            },
            {
                Header: "Genre",
                accessor: "genre"
            },
            {
                Header: "Duration",
                accessor: "duration"
            },
            {
                Header: "Download",
                accessor: "download"
            }
        ],
        []
    );

    useEffect(() => {
        getData();
    }, []);

    useEffect(() => {
        fillPlaylist();
    }, [cells]);

    const data = React.useMemo(() => cells, [cells]);

    return (
        <div className="content">
            <div>
                <h1 style={{color: "black", textAlign: "center"}}>Playlist</h1>
            </div>
            {cells && <PlaylistTable columns={columns} data={data}/>}
            <div>
                <ReactJkMusicPlayer
                    preload={true}
                    mode="full"
                    theme="dark"
                    showDownload={true}
                    audioLists={playlist}
                    showMediaSession
                    autoPlay={false}
                    defaultPosition={{top: 100, left: 0}}
                />
            </div>
        </div>
    )
}

export default PlaylistContent;