import {Button} from "@mui/material";
import React from "react";
import {ShoppingCartInstance} from "../shoppingcart/ShoppingCartInstance";
import {Song} from "../Song";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import {styled} from "@mui/material/styles";
import {ButtonProps} from "@mui/material/Button";
import {lightGreen} from "@mui/material/colors";

function SearchResultEntry({song}: { song: Song }) {

    function addToCart(song: Song) {
        const shoppingCartInstance = ShoppingCartInstance.getInstance();
        shoppingCartInstance.addItem(song);
    }

    const ColorButton = styled(Button)<ButtonProps>(({theme}) => ({
        color: theme.palette.getContrastText(lightGreen[500]),
        backgroundColor: lightGreen[500],
        borderColor: lightGreen[500],
        '&:hover': {
            color: theme.palette.getContrastText(lightGreen[700]),
            backgroundColor: lightGreen[700],
            borderColor: lightGreen[700],
        },
    }));

    return (
        <div className="search-result-display">
            <img src={`https://music-shop-songs.s3.eu-central-1.amazonaws.com/covers/${song.id}.jpg`} width="150"
                 height="150"></img>
            <ul style={{textAlign: 'left'}}>

                <h3>{song.title}</h3>
                <p>{song.artist}</p>
                <p>{song.genre}</p>
            </ul>
            <div className="search-result-display-right-side">
                <h3>Price: {song.price}</h3>
                <ColorButton onClick={() => addToCart(song)} endIcon={<AddShoppingCartIcon/>} variant="outlined">
                    Add to cart
                </ColorButton>
            </div>
        </div>
    )
}

export default SearchResultEntry;